.action-group {
  display: flex;
  gap: 20px;
  justify-content: flex-end;
  margin-bottom: 14px; }
  .action-group .btn-tip {
    position: relative; }
    .action-group .btn-tip::after {
      content: '';
      position: absolute;
      top: 2px;
      right: -3px;
      width: 16px;
      height: 16px;
      background-color: #F76195;
      border-radius: 50%;
      border: 2px solid #EEEEEE;
      display: none; }
    .action-group .btn-tip.new::after {
      display: block; }
  .action-group button {
    background: none;
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease; }
    .action-group button img {
      height: 30px; }
    .action-group button:hover {
      transform: scale(1.05); }

.dropdown {
  position: relative; }
  .dropdown .dropdown-menu {
    background-color: #fff;
    border: 1px solid #005E53;
    border-radius: 12px;
    padding: 12px;
    margin-top: 8px;
    min-width: 220px; }
    .dropdown .dropdown-menu > li ~ li {
      border-top: 1px dashed #D9D9D9; }
    .dropdown .dropdown-menu a:hover {
      color: #005E53; }
    .dropdown .dropdown-menu .dropdown-menu-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 8px 0;
      text-decoration: none;
      font-size: 0.875rem;
      transition: 0.3s ease;
      color: #000; }
      .dropdown .dropdown-menu .dropdown-menu-item .badge {
        background-color: #F76195;
