        text-align: center; }

.btn-tooltip {
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  border-radius: 50%;
  color: #5C5C5C;
  font-size: 24px; }

.agreement-panel {
  background-color: #F5F5F5; }
  .agreement-panel .form-check {
    transition: all 0.3s ease; }
    .agreement-panel .form-check label {
      opacity: 0.5;
